* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Open Sans', sans-serif;
  background: #141414;
  color: #ECF0F1;
}
h1, h2 {
  font-family: 'Montserrat', sans-serif;
  color: #18BC9C;
}
a {
  text-decoration: none;
  color: inherit;
}
main {
  padding-top: 70px;
}
.navbar-container {
  position: fixed;
  top: 0; width: 100%;
  background: rgba(30,30,30,0.7);
  display: flex; justify-content: space-between; align-items: center;
  padding: 1rem 2rem;
  backdrop-filter: blur(10px);
  z-index: 1000;
}
.logo { font-size: 1.5rem; color: #18BC9C; }
.hamburger {
  background: none; border: none;
  color: #ECF0F1; font-size: 1.8rem;
  display: none;
}
.nav-links a { margin-left: 1.5rem; font-weight: 600; }
.nav-links.mobile-active {
  display: flex; flex-direction: column;
  position: absolute; top: 100%; left: 0;
  width: 100%; background: rgba(20,20,20,0.9);
  padding: 1rem;
}
@media (max-width: 768px) {
  .hamburger { display: block; }
  .nav-links { display: none; }
}
.glass-section {
  margin: 2rem auto; padding: 2rem;
  max-width: 900px;
  background: rgba(44,62,80,0.5);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  border: 1px solid rgba(255,255,255,0.1);
}
.glass-section p,
.glass-section li {
  white-space: normal;
  overflow: visible;
  text-overflow: unset;
  margin-top: 1rem;
  line-height: 1.6;
  font-size: 1.05rem;
}
.hero-content { text-align: center; }
.profile-pic {
  width:160px; height:160px;
  border-radius: 50%;
  border:4px solid #18BC9C;
  object-fit: cover;
  margin-bottom: 1rem;
}
.projects-container {
  display: grid;
  gap:1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.project-card {
  background:rgba(52,73,94,0.7);
  padding:1rem; border-radius:10px;
  box-shadow:0 4px 8px rgba(0,0,0,0.3);
  transition: transform 0.3s;
}
.project-card:hover { transform: translateY(-5px); }
.project-card h3 { margin-bottom:0.5rem; color:#18BC9C; }
.project-links {
  display:flex; gap:1rem;
  margin-top:1rem;
  flex-wrap: wrap;
}
.project-links .btn {
  background:#18BC9C; color:#fff;
  padding:0.5rem 1rem; border-radius:5px;
  font-weight:600; transition: background 0.3s ease;
}
.project-links .btn:hover { background:#1ABC9C; }
.skills-list {
  display:flex; flex-wrap:wrap; gap:1rem;
  list-style:none; margin-top:1rem;
}
.skills-list li {
  background:#18BC9C; padding:0.5rem 1rem;
  border-radius:5px; font-weight:bold; color:#fff;
}
.resume-btn {
  display:inline-block; margin-top:1rem;
  background:#18BC9C; padding:0.75rem 1.25rem;
  border-radius:5px; font-weight:bold; color:#fff;
  transition:background 0.3s ease;
}
.resume-btn:hover { background:#1ABC9C; }
.contact-form {
  display:flex; flex-direction:column; gap:1rem;
  margin-top:1rem;
}
.contact-form input,
.contact-form textarea {
  width:100%; padding:0.75rem;
  border-radius:5px; border:1px solid #ccc;
  font-size:1rem;
}
.contact-form button {
  background:#18BC9C; color:#fff;
  border:none; padding:0.75rem 1.5rem;
  border-radius:5px; font-size:1rem;
  cursor:pointer; transition:background 0.3s;
}
.contact-form button:hover { background:#1ABC9C; }
@media (max-width: 600px) {
  h1 { font-size: clamp(1.5rem, 5vw, 2rem); }
  h2 { font-size: clamp(1.25rem, 4vw, 1.75rem); }
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background: url('background.jpg') no-repeat center center fixed;
  background-size: cover;
  color: #fff;
}

